.itemName {
  display: block;
  margin: 0;
  padding: 0;
  font-size: 14px;
}

.A4 {
  display: flex;
  width: 1240px;
  height: 1754px;
  padding-top: 83px;
  background-color: white;
  justify-content: center;
  align-content: flex-start;
  flex-wrap: wrap;
  overflow: hidden;
}

.topBar {
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  position: sticky; 
  top: 0px;
  background: #eeeeee;
  z-index: 20;
  text-align: center;
}

.download {
  width:80px;
  height:30px;
  background:#ff8874;
  border:none;
  margin:3px;
  font-size: 15px;
  font-weight: bold;
  color: white;
  cursor: default;
  -ms-user-select: none; 
  -moz-user-select: -moz-none; 
  -webkit-user-select: none; 
  -khtml-user-select: none; 
  user-select:none;
}
